<template>
  <div>profile</div>
</template>
<script>
import { mapState } from "vuex";

export default {
  name: "profile",
  computed: {
    ...mapState(["tab"])
  },
  created() {},
  activated() {
    if (this.tab !== "profile") {
      this.$store.commit("changeTab", "profile");
    }
  }
};
</script>



